* {
    box-sizing: border-box;
    font-family: 'Varela Round', sans-serif;
    line-height: 1.6;
}

.wrapper {    
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

a {
    text-decoration: none;
    color: #17a2b8;
}

.main-footer {
    background: darkgray;
    color: #17a2b8;
    padding: 2em 0;
    margin-top: 30px;
    text-align: center;    
}

.container ul {
    padding: 0;
    list-style: none;
}

.container {
    width: 95%;
    margin: 0 auto;
}

.name {
    font-size: 1.25em;
}

.main-nav a {
    font-size: .95em;
    color: #222;
    text-transform: uppercase;        
}

.main-nav a:hover {
    color: #17a2b8;
}

.main-nav {
    text-align: center;
}

.flex-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

h5, h6 {
    margin: 0.3rem auto;
}

img {
    margin: 0;
}

.back-button {
    margin: 5% 40%;
    border: none;
    padding: 1% 4%;
    cursor: pointer;
}

.cart_container {
    display: flex;
    align-content: space-around;
    height: 200px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 90%;
    margin-left: 2%;
    margin-top: 2%;
}

.signed {
    display: flex;
    flex-flow: column;
    align-self: center;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 80%;
    padding: 3%;
}

.signed label {
    padding-bottom: 2%;
    padding-top: 2%;
    display: flex;
    flex-flow: column;
}
.signed input {
    padding: 3%;
}

#sign {
    background-image:linear-gradient(90deg,#5082e4,#52bcf8);;
    border: none;
    color: white;
    padding: 4% 3%;
    margin-top: 3%;
    text-decoration: none;
    cursor: pointer;
    width: 100%;
    align-self: center;
}

.cart-img {
    height: 200px;
    width: 20%;
}

.entry-block {
    margin: 2rem;
    width: 90%;
}

.entry-block input {
    margin-bottom: 2%;
    width: 50%;
}

.entry-block div {
    display: flex;
}

.entry-img {
    width: 80%; 
}

.add {
    background-color: #429bfc;
    border: 1px solid #3393fc;
    border-radius: 3px;
    color: white;
    padding: 0 12%;
    text-decoration: none;
    margin: 1% 2%;
    cursor: pointer;
    height: 50px;
}



@media (min-width: 769px) {
    .entry-block {
        margin: 2rem;
        width: 30%;
    }

    .signed {
        display: flex;
        flex-flow: column;
        align-self: center;
        background-color: white;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        width: 50%;
        padding: 3%;
    }

    .wrapper {
        padding-top: 130px;
        height: 100vh;
    }

    .main-header {
        position: fixed;
        background: #fff;
        box-shadow: 0 1px 4px rgba(0,0,0,.4);
        width: 100%; 
        top: 0;   
        z-index: 1000; 
    }

    .main-nav {
        display: flex;
    }

    .main-nav li {
        padding: .5em;
    }

    .container {     
        display: flex;   
        flex-direction: column;
        align-items: center;
        width: 90%;
        max-width: 1150px;
        margin: 0 auto;
    } 
}
@media (min-width: 1050px) {
    .entry-block {
        margin: 2rem;
        width: 30%;
    }

    .signed {
        display: flex;
        flex-flow: column;
        align-self: center;
        background-color: white;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        width: 35%;
        padding: 3%;
    }

    .wrapper {
        padding-top: 100px;
    }

    .container {
        flex-direction: row;
        justify-content: space-between;
    }

    .main-nav li {
        padding: .5em;
        margin-top: 13px;
    }
}